.home_heading{
    color: #00008b;
    font-size: 1rem;
    display: flex;
    flex-direction: row;
    font-weight: bold;
    padding: 1rem;
    font-family: Georgia, 'Times New Roman', Times, serif;    
}

.home_heading p:nth-of-type(2)
{
    margin-left:2rem;
    font-weight: normal;
    font-size: 1rem;
}

.dashboard_deatils{
    display: flex;
    flex-direction: column;
    align-items: center;
}

.front_page_vector_484{
    display: flex;
    flex-direction: row;
    justify-content: center;
    flex-wrap: wrap;
}

.cases_123{
    display: grid;
    grid-template-columns: repeat(2,2fr);
    grid-template-rows: repeat(2,2fr);
    grid-column-gap: 2rem;
    grid-row-gap: 2rem;
    justify-items: center;
    width: max-content;
    background: white;
    padding: 1rem;
    margin: 1rem;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
    border-radius: 2rem;
}

.data_J818{
    display: flex;
    flex-direction: column;
    align-items: center;
    color: black;
    height: min-content;
    width: 10rem;
    margin: 0.2rem; 
    font-size: 1rem;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
    border-radius: 2rem;
    background: white;
    padding: 0.5em;
}

.vector_456{
    display: flex;
    flex-direction: row;
    font-weight: bold;
    align-content: center;
}

.vector_456 img{
    margin-left: 0.7rem;
    width: 2.5rem;
    height: 2.7rem;
}

.count_3443{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.count_3443 p:nth-child(2){
    margin-left: 0.3rem;
    font-size: 0.7rem;
    margin-top:1.2rem; 
}

.data_stack{
    display: flex;
    flex-direction: column;
    align-items:  center;
    justify-content: space-evenly;
}

@media(max-width:45rem){
    .cases_123{
        grid-row-gap: 1rem;
        grid-column-gap: 1rem;
        margin: 0.4rem;
    }
    .data_J818{
        width: 7rem;
        height: 7rem;
    }
    .vector_456 img{
        width: 1.5rem;
        height: 1.5rem;
        margin-top: 0.8rem;
    }
}