.table_background{
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 1rem;
    color: black;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
    text-align: left;
    border-radius: 2rem;
    background: #ffffff;
    padding: 1rem;
}
th,td,tr{
    padding: 1rem;
    border-bottom: 2px solid #ddd;
}



td,tr{
    font-weight: bold;
}

.mob_list_view{
    display: grid;
    grid-template-columns: repeat(2,120px);
    border-bottom: 2px solid #ddd;
}

.mob_list_col
{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
    padding: 0.5rem;
    border-radius: 1rem;
    margin: 0.2rem
}

.mob_view_back{
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    margin: 0.7rem;
    width: min-content;
    color: black;
    font-weight: bold;
    box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.5);
    text-align: left;
    border-radius: 2rem;
    background: #ffffff;
    padding: 1rem;
}

